<template>
<div class="right-content">
    <topbar bone="" :btwo="btwo"></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="parentId">
                        <el-select v-model="form.parentId" placeholder="" no-data-text="">
                            <el-option :label="item.title" :value="item.id" :key="item.id" v-for="item in typeList.list"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="title">
                        <el-input v-model="form.title" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">

            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submit('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// /
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'

export default {
    data() {
        return {
            btwo: '',
            form: {
                parentId: '',
                title: ''
            },
            switchStatus: true,
            rules: {
                parentId: [{ required: true, message: '',trigger: 'blur' }],
                title: [{ required: true, message: '',trigger: 'blur' }]
            }
        }
    },
    created() {
        this.$store.dispatch('getDictionaryTypeList', {})
    },
    computed: {
        typeList() { return this.$store.getters.dictionaryTypeList }
    },
    methods: {
        submit(formName) {
            let that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    let param = this.form
                    param.status = this.switchStatus === true ? 1 : 0

                    api.apiCommunication('/Material/CreateType', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })

                } else {
                    this.$alert('', '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    },
    components: {
        topbar,
        cancel
    }
}
</script>



