<template>
<div id="staffmenteditor" class="right-content">
    <topbar bone="" btwo=""></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="account">
                        <el-input v-model="form.account"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="password">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="position">
                        <el-input v-model="form.position"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model="form.phone"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">
                <image-upload :imageUrl="imageUrl" @increment="handleAvatarScucess"></image-upload>
                <p class="mt-10"></p>
            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <div class="tableTopbar"><span></span></div>
            <el-table :data="boxlist" :stripe="true" class="w-100 mt-10" max-height="540" @selection-change="handleSelectionChange">
                <el-table-column prop="province" label="" width="90" :filters="provinceList" :filter-method="filterProvince"></el-table-column>
                <el-table-column prop="city" label="" width="100" :filters="cityList" :filter-method="filterCity"></el-table-column>
                <el-table-column prop="area" label="" width="110" :filters="areaList" :filter-method="filterArea"></el-table-column>
                <el-table-column prop="community" label="" width="170"></el-table-column>
                <el-table-column prop="addr" label=""></el-table-column>
                <el-table-column type="selection" label="" width="55"></el-table-column>
            </el-table>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submitForm('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import imageUpload from '../common/imageUpload.vue'
import '../../static/style/staffment/tallymanEditor.scss'

export default {
    data() {
        return {
            form: {
                account: '',
                password: '',
                name: '',
                position: '',
                phone: ''
            },
            imageUrl: '',
            switchStatus: true,
            rules: {
                account: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
                ],
                name: [ { required: true, message: '', trigger: 'blur' } ],
                position: [ { required: true, message: '', trigger: 'blur' } ],
                phone: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 11, max: 11, message: '11', trigger: 'blur' }

                ]
            },
            selectBox: []
        }
    },
    created() {
        this.$store.dispatch('getBoxList',{ page: 0 })
    },
    computed: {
        boxlist() { return this.$store.getters.boxList },
        provinceList() {
            let _data = this.boxlist,
                _list = [],
                _filter = []
            for(let item of _data) {
                if(!_filter.includes(item.province)){
                    _filter.push(item.province)
                }
            }
            for(let item of _filter) {
                _list.push({ text: item, value: item })
            }
            return _list
        },
        cityList() {
            let _data = this.boxlist,
                _list = [],
                _filter = []
            for(let item of _data) {
                if(!_filter.includes(item.city)){
                    _filter.push(item.city)
                }
            }
            for(let item of _filter) {
                _list.push({ text: item, value: item })
            }
            return _list
        },
        areaList() {
            let _data = this.boxlist,
                _list = [],
                _filter = []
            for(let item of _data) {
                if(!_filter.includes(item.area)){
                    _filter.push(item.area)
                }
            }
            for(let item of _filter) {
                _list.push({ text: item, value: item })
            }
            return _list
        }
    },
    methods: {
        submitForm(formName) {
            var that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = this.form
                    param.boxes = ''
                    for (let item of this.selectBox) {
                        if (param.boxes === '') {
                            param.boxes += item.box_no
                        } else {
                            param.boxes += ',' + item.box_no
                        }
                    }
                    param.img = this.imageUrl
                    param.status = this.switchStatus === true ? 1 : 0

                    api.apiCommunication('/Tallyman/CreateTally', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })
                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        },
        handleAvatarScucess(row) {
            // 
            this.imageUrl = row
        },
        filterProvince(value, row) {
            return row.province === value
        },
        filterCity(value, row) {
            return row.city === value
        },
        filterArea(value, row) {
            return row.area === value
        },
        handleSelectionChange(val) {
            this.selectBox = val
        }
    },
    components: {
        topbar,
        cancel,
        imageUpload
    }
}
</script>



