<template>
<div id="productmgt" class="right-content">
    <topbar bone="" btwo=""></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="90px">

                    <el-form-item label="ID">
                        <el-input placeholder="" :disabled="true"></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="title">
                        <el-input v-model="form.title" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="code">
                        <el-input v-model="form.code" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="first_class">
                        <el-select v-model="form.first_class" placeholder="" @change="getMediumclass">
                            <el-option :label="item.number + ' ' + item.title" :value="item.id" :key="item.id" v-for="item in bigclass">
                                {{ item.number }} {{ item.title }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="second_class">
                        <el-select v-model="form.second_class" placeholder="" no-data-text="/" @change="getSmallclass">
                            <el-option :label="item.number + ' ' + item.title" :value="item.id" :key="item.id" v-for="item in mediumclass">
                                {{ item.number }} {{ item.title }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="thirth_class">
                        <el-select v-model="form.thirth_class" placeholder="" no-data-text="/">
                            <el-option :label="item.number + ' ' + item.title" :value="item.id" :key="item.id" v-for="item in smallclass">
                                {{ item.number }} {{ item.title }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="packing_id">
                        <el-select v-model="form.packing_id" placeholder="">
                            <el-option :label="item.title" :value="item.id" :key="item.id" v-for="item in packing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="spec">
                        <el-input v-model="form.spec" placeholder="500g"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="price">
                        <el-input v-model.number="form.price" placeholder="6.88" @change="changePrice">
                            <template slot="append"></template>
                        </el-input>
                    </el-form-item>

                    <div class="gross-margin">
                        <el-form-item label="" prop="purchasePrice">
                            <el-input v-model.number="purchasePrice" placeholder="" @change="changePurchasePrice">
                                <template slot="append"></template>
                            </el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="grossMargin" placeholder="" :disabled="true">
                                <template slot="append">%</template>
                            </el-input>
                        </el-form-item>
                    </div>

                    <el-form-item label="">
                        <el-input v-model="form.time_validity" placeholder="">
                            <template slot="append"></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.yu_validity" placeholder="">
                            <template slot="append"></template>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.orgin" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" class="supplier">
                        <el-input :value="supplierNum" :disabled="true"></el-input>

                        <el-popover ref="addsupplier" placement="right" width="400" trigger="click" v-model="popoverVisible">

                            <el-form ref="supplierForm" :rules="supplierRules" label-position="right" :model="supplierForm" label-width="120px" class="supplier-form">

                                <el-form-item label="" prop="name">
                                    <el-select v-model="supplierForm.name" filterable placeholder="">
                                        <el-option :label="item.company" :value="item.id + ',' + item.company" :key="item.id" v-for="item in supplierSelectList"></el-option>
                                    </el-select>
                                </el-form-item>

                                <el-form-item label="" prop="bprice">
                                    <el-input v-model.number="supplierForm.bprice" placeholder="">
                                        <template slot="append"></template>
                                    </el-input>
                                </el-form-item>

                                <el-form-item label="" class="supplier-submitform">
                                    <el-button type="primary" @click="submitSupplier('supplierForm')"></el-button>
                                </el-form-item>
                            </el-form>

                        </el-popover>

                        <el-button type="primary" v-popover:addsupplier></el-button>
                    </el-form-item>

                    <el-form-item class="supplier-list" label-width="0">
                        <el-table :data="supplier" :stripe="true" class="w-100">
                            <el-table-column prop="name" label="" width="218"></el-table-column>
                            <el-table-column prop="bprice" label="" width="100">
                                <template scope="scope">
                                    {{ scope.row.bprice }} 
            					</template>
                            </el-table-column>
                            <el-table-column label="" width="80">
                                <template scope="scope">
                                    <el-button type="danger" size="small" @click="deleteSupplier(scope.$index)"></el-button>
            					</template>
            			  	</el-table-column>
            			</el-table>
                    </el-form-item>

                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">
                <image-upload :imageUrl="imageUrl" @increment="handleAvatarScucess"></image-upload>
                <p class="mt-10"></p>
            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submit('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import imageUpload from '../common/imageUpload.vue'
import '../../static/style/datament/productMgt.scss'

export default {
    data() {
        return {
            imageUrl: '',
            form: {
                title: '',
                code: '',
                first_class: '',
                second_class: '',
                thirth_class: '',
                packing_id: '',
                spec: '',
                price: '',
                time_validity: '',
                yu_validity: '',
                orgin: ''
            },
            supplierForm: {
                name: '',
                bprice: ''
            },
            supplierNum: '0',
            popoverVisible: false,
            purchasePrice: '',
            grossMargin: '',
            switchStatus: true,
            rules: {
                title: [{ required: true, message: '', trigger: 'blur' }],
                code: [{ required: true, message: '', trigger: 'blur' }],
                first_class: [{ required: true, message: '', trigger: 'change' }],
                second_class: [{ required: true, message: '', trigger: 'change' }],
                thirth_class: [{ required: true, message: '', trigger: 'change' }],
                packing_id: [{ required: true, message: '', trigger: 'change' }],
                spec: [{ required: true, message: '', trigger: 'blur' }],
                price: [
                     { required: true, message: ''},
                    { type: 'number', message: ''}
                ],
                purchasePrice: [{ type: 'number', message: ''}]
            },
            supplierRules: {
                name: [{ required: true, message: '', trigger: 'change' }],
                bprice: [
                    { required: true, message: ''},
                   { type: 'number', message: ''}
                ]
            },
            supplier: []
        }
    },
    created() {
        // 
        // 
        // 
        this.$store.dispatch('resetClass')
        this.$store.dispatch('getProductBigClass')
        this.$store.dispatch('getDictionarySearchList', { code: 'packing' })
        this.$store.dispatch('getProductSupplier')
    },
    computed: {
        bigclass() { return this.$store.getters.productBigclassList },
        mediumclass() { return this.$store.getters.productMediumclassList },
        smallclass() { return this.$store.getters.productSmallclassList },
        packing() { return this.$store.getters.dictionarySearchList },
        supplierSelectList() { return this.$store.getters.productSupplier }
    },
    methods: {
        submit(formName) {
            let that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    let param = this.form
                    param.status = this.switchStatus === true ? 1 : 0
                    param.supplier = JSON.stringify(this.supplier)
                    param.img = this.imageUrl

                    api.apiCommunication('/Product/CreateProduct', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })

                } else {
                    this.$alert('', '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        submitSupplier(formName) {
            // 
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    let _item = {
                        id: this.supplierForm.name.split(',')[0],
                        name: this.supplierForm.name.split(',')[1],
                        bprice: this.supplierForm.bprice
                    }
                    // 
                    if(this.supplier.find((n) => n.id === _item.id) === undefined) {
                        // 
                        this.supplier.push(_item)
                        this.supplierNum = '' + this.supplier.length + ''
                        // 
                        this.popoverVisible = false
                    } else {
                        this.$alert('', '', { confirmButtonText: '', type: 'error' })
                    }
                } else {
                    this.$alert('', '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        deleteSupplier(val) {
            this.supplier.splice(val,1)
            this.supplierNum = '' + this.supplier.length + ''
        },
        handleAvatarScucess(val) {
            // 
            this.imageUrl = val
        },
        changePrice(val) {
            // 
            if(this.purchasePrice !== ''){
                this.calculationGrossMargin(val, this.purchasePrice)
            }
        },
        changePurchasePrice(val) {
            // 
            if(this.form.price !== ''){
                this.calculationGrossMargin(this.form.price, val)
            }
        },
        calculationGrossMargin(price, purchasePrice) {
            // 
            let _grossMargin = (price - purchasePrice)/price*100
            this.grossMargin = _grossMargin.toFixed(2)
        },
        getMediumclass(val) {
            // 
            this.$store.dispatch('getProductMediumClass', { id: val })
        },
        getSmallclass(val) {
            // 
            this.$store.dispatch('getProductSmallClass', { id: val })
        }
    },
    components: {
        imageUpload,
        topbar,
        cancel
    }
}
</script>



