<template>
    <div>
        <el-upload class="image-upload" :action="actionUrl" :show-file-list="false" :on-success="handleAvatarScucess" :on-error="handleAvatarError" :before-upload="beforeAvatarUpload" drag>
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
        <a :href="imageUrl" class="f-color f-link" target="_blank" v-if="imageUrl"></a>
    </div>
</template>

<script>
// 
import '../../static/style/common/imageUpload.scss'

export default {
    props: ['imageUrl'],
    data() {
        return {
            actionUrl: 'http://img.bingofresh.com/admin/Upload.php'
        }
    },
    methods: {
        beforeAvatarUpload(file) {
            // 
            const isJPG = (file.type === 'image/jpeg' || file.type === 'image/png')
            const isLt2M = file.size / 1024 / 1024 < 2

            if (!isJPG) {
              this.$message.error(' JPG/PNG !')
            }
            if (!isLt2M) {
              this.$message.error(' 2MB!')
            }
            return isJPG && isLt2M
        },
        handleAvatarScucess(response, file) {
            // 
            if(response.status === 404) {
                this.$alert(response.data, '', { confirmButtonText: '', type: 'error' })
                return false
            }
            this.$emit('increment',response.data)
        },
        handleAvatarError(err, file) {
            // 
            this.$alert('', '', { confirmButtonText: '', type: 'error' })
        }
    }
}
</script>



