<template>
<div id="login">
    <div class="login-header">
        <div class="header-logo">
            <h1>
                <a href="">
                    <img src="../../static/img/logo.png" alt="logo">
                </a>
            </h1>
            <div class="header-link"><a href="" target="_blank"></a><span>|</span><a href="http://www.binguohezi.com/" target="_blank"></a></div>
        </div>
    </div>
    <div class="login-content">
        <div class="content-wrap">
            <div class="content-title">
                <div class="title-wrap">
                    <img src="../../static/img/login-tips.png" alt="">
                </div>
            </div>
            <div class="content-form">
                <div class="form-wrap">
                    <h2>USER LOGIN</h2>
                    <h1></h1>
                    <el-form :model="form" :rules="rules" ref="form">
                        <el-form-item prop="username">
                            <el-input placeholder="" v-model="form.username" class="mt-20" size="large">
                                <template slot="prepend">
                                    <i class="iconfont icon-account fs-1s2em"></i>
                                </template>
    						</el-input>
    					</el-form-item>
    					<el-form-item prop="password">
    						<el-input type="password" placeholder="" v-model="form.password" class="mt-20" size="large">
    						    <template slot="prepend">
                                    <i class="iconfont icon-Password fs-1s2em"></i>
                                </template>
    						</el-input>
    					</el-form-item>
    					<el-button type="primary" @click="onLogin('form')" class="content-login"></el-button>
    				</el-form>
                </div>
            </div>
        </div>
    </div>
    <div class="login-footer">
        <p></p>
        <p>ICP16097902-1</p>
    </div>
</div>
</template>

<script type="text/javascript">
// 
import '../../static/style/logins/login.scss'

// 
export default {
    data() {
        return {
            form: {
                username: '',
                password: ''
            },
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 20, message: ' 5  20 ', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        onLogin(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    this.$store.dispatch('onLogin', this.form)
                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            });
        }
    }
}
</script>



