<template>
<div class="right-content">
    <topbar bone="" btwo=""></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">

                    <el-form-item label="" prop="province">
                        <el-select v-model="form.province" placeholder="" @change="getCityList">
                            <el-option :label="item.code + ' ' + item.name" :value="item.id" :key="item.id" v-for="item in provincelist">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="city">
                        <el-select v-model="form.city" placeholder="" no-data-text="" @change="getArea">
                            <el-option :label="item.code + ' ' + item.name" :value="item.id" :key="item.id" v-for="item in citylist">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="parent_id">
                        <el-select v-model="form.parent_id" placeholder="" no-data-text="">
                            <el-option :label="item.code + ' ' + item.name" :value="item.id" :key="item.id" v-for="item in areaList">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="address">
                        <el-input v-model="form.address"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="community">
                        <el-input v-model="form.community"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right"></div>
        </el-col>
    </el-row>

    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submitForm('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'

export default {
    data() {
        return {
            param: this.$route.params.id,
            form: {
                province: '',
                city: '',
                parent_id: '',
                address: '',
                community: ''
            },
            switchStatus: true,
            rules: {
                province: [{ required: true, message: '', trigger: 'change' }],
                city: [{ required: true, message: '', trigger: 'change' }],
                parent_id: [{ required: true, message: '', trigger: 'change' }],
                address: [{ required: true, message: '', trigger: 'blur' }],
                community: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created() {
        this.$store.dispatch('getProvinceList', {})
    },
    computed: {
        provincelist() { return this.$store.getters.provinceList },
        citylist() { return this.$store.getters.cityList },
        areaList() { return this.$store.getters.areaList }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    let param = this.form,
                        that = this
                    param.status = this.switchStatus === true ? 1 : 0

                    api.apiCommunication('/City/CreateCommunity', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })

                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        },
        getCityList(val) {
            this.$store.dispatch('getCityList', { id: val })
        },
        getArea(val) {
            this.$store.dispatch('getAreaList', { id: val })
        }

    },
    components: {
        topbar,
        cancel
    }
}
</script>



