<template>
<div id="boxeditor" class="right-content">
    <topbar bone="" btwo=""></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="box_no">
                        <el-input v-model="form.box_no" placeholder="box_noA1001"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.access_number" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="province">
                        <el-select v-model="form.province" placeholder="" @change="getCity">
                            <el-option :label="item.code + ' ' + item.name" :value="item.code + ',' + item.id + ',' + item.name" :key="item.id" v-for="item in provincelist">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="city">
                        <el-select v-model="form.city" placeholder="" no-data-text="" @change="getArea">
                            <el-option :label="item.code + ' ' + item.name" :value="item.code + ',' + item.id + ',' + item.name" :key="item.id" v-for="item in citylist">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="area">
                        <el-select v-model="form.area" placeholder="" no-data-text="" @change="getCommunity">
                            <el-option :label="item.code + ' ' + item.name" :value="item.code + ',' + item.id + ',' + item.name" :key="item.id" v-for="item in areaList">
                                {{ item.code }} {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="community">
                        <el-select v-model="form.community" placeholder="" no-data-text="">
                            <el-option :label="item.name" :value="item.name" :key="item.id" v-for="item in communityList">
                                {{ item.name }}
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.addr" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="display_name">
                        <el-input v-model="form.display_name" placeholder="A1001"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="realnameStatus"></el-switch>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="humanfaceStatus"></el-switch>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="ownerStatue"></el-switch>
                    </el-form-item>
                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">
                <a href="http://lbs.amap.com/api/javascript-api/example/map/click-to-get-lnglat" target="_blank" class="editor-coordinate mb-15"></a>
                <el-input v-model="form.apoints" placeholder=""></el-input>
            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submit('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import '../../static/style/boxment/boxEditor.scss'

export default {
    data() {
        return {
            form: {
                box_no: '',
                access_number: '',
                province: '',
                city: '',
                area: '',
                community: '',
                addr: '',
                display_name: '',
                apoints: ''
            },
            realnameStatus: false,
            humanfaceStatus: false,
            ownerStatue: false,
            switchStatus: true,
            rules: {
                box_no: [{ required: true, message: '', trigger: 'blur' }],
                province: [{ required: true, message: '', trigger: 'change' }],
                city: [{ required: true, message: '', trigger: 'change' }],
                area: [{ required: true, message: '', trigger: 'change' }],
                display_name: [{ required: true, message: '', trigger: 'blur' }]
            }
        }
    },
    created() {
        this.$store.dispatch('resetUnitList')
        this.$store.dispatch('getBoxList', {})
        this.$store.dispatch('getProvinceList', {})
    },
    computed: {
        provincelist() { return this.$store.getters.provinceList },
        citylist() { return this.$store.getters.cityList },
        areaList() { return this.$store.getters.areaList },
        communityList() { return this.$store.getters.communityList }
    },
    methods: {
        submit(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = {
                        box_no: this.form.box_no,
                        access_number: this.form.access_number,
                        province: this.form.province.split(',')[2],
                        province_code: this.form.province.split(',')[0],
                        city: this.form.city.split(',')[2],
                        city_code: this.form.city.split(',')[0],
                        area: this.form.area.split(',')[2],
                        area_code: this.form.area.split(',')[0],
                        community: this.form.community,
                        addr: this.form.addr,
                        display_name: this.form.display_name,
                        apoints: this.form.apoints,
                        status: this.switchStatus ? 1 : 0,
                        realname: this.realnameStatus ? 1 : 0,
                        face: this.humanfaceStatus ? 1 : 0,
                        owner: this.ownerStatue ? 1 : 0
                    }
                    let that = this
                    api.apiCommunication('/Box/CreateBox', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })

                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        },
        getCity(val) {
            // 
            this.$store.dispatch('getCityList', { id: val.split(',')[1] })
        },
        getArea(val) {
            // 
            this.$store.dispatch('getAreaList', { id: val.split(',')[1] })
        },
        getCommunity(val) {
            // 
            this.$store.dispatch('getCommunityList', { id: val.split(',')[1] })
        }
    },
    components: {
        topbar,
        cancel
    }
}
</script>



