/**
 * 
 */
import * as types from '../mutation'


const state = {
    headernav: [],
    elmenu: []
}

const getters = {
    headernav: state => state.headernav,
    elmenu: state => state.elmenu
}

// 
const sellmentdata = [
    {
        name: '',
        url: '/generality',
        child: [{
            name: '',
            url: '/sellment/selloverview',
        }]
    },
    {
        name: '',
        url: '/product',
        child: [{
            name: '',
            url: '/sellment/productsales'
        }]
    },
    {
        name: '',
        url: '/order',
        child: [{
            name: '',
            url: '/sellment/ordersearch'
        }]
    }
]
// 
const boxmentdata = [
    {
        name: '',
        url: '/boxmentmgt',
        child: [{
            name: '',
            url: '/boxment/boxlist'
        }]
    },
    {
        name: '',
        url: '/regionmgt',
        child: [
            {
                name: '',
                url: '/boxment/province'
            },
            {
                name: '',
                url: '/boxment/city'
            },
            {
                name: '',
                url: '/boxment/area'
            },
            {
                name: '',
                url: '/boxment/community'
            }
        ]
    }
]
// 
const usermentdata = [
    {
        name: '',
        url: '/userment',
        child: [{
                name: '',
                url: '/userment/userlist'
            }
            // {
            //     name: '',
            //     url: '/userment/statistics'
            // }
        ]
    }
]

// 
const staffmentdata = [
    {
        name: '',
        url: '/staffment',
        child: [{
            name: '',
            url: '/staffment/tallymanlist'
        }]
    }
]

// 
const datamentdata = [
    {
        name: '',
        url: '/supplier',
        child: [{
            name: '',
            url: '/datament/supplier'
        }]
    },
    {
        name: '',
        url: '/productclass',
        child: [
            {
                name: '',
                url: '/datament/bigclass'
            },
            {
                name: '',
                url: '/datament/mediumclass'
            },
            {
                name: '',
                url: '/datament/smallclass'
            }
        ]
    },
    {
        name: '',
        url: '/prodyctdictionary',
        child: [
            {
                name: '',
                url: '/datament/dictionarytype'
            },
            {
                name: '',
                url: '/datament/pdictionary'
            }
        ]
    },
    {
        name: '',
        url: '/productment',
        child: [
            {
                name: '',
                url: '/datament/productmgt'
            }
        ]
    }
]


const actions = {
    /**
     * 
     */
    headernavSwitch({ commit }) {
        let data = [
            {
                name: '',
                url: '/sellment',
                type: 'sellment'
            },
            {
                name: '',
                url: '/overview',
                type: 'overview'
            },
            {
                name: '',
                url: '/boxment',
                type: 'boxment'
            },
            {
                name: '',
                url: '/userment',
                type: 'userment'
            },
            {
                name: '',
                url: '/staffment',
                type: 'staffment'
            },
            {
                name: '',
                url: '/datament',
                type: 'datament'
            }
        ]
        commit(types.HEADERNAV_SWITCH, { data })
    },
    /**
     * 
     */
    elmenuSwitch({ commit }, type) {
        let data
        switch (type) {
            case 'sellment':
                // 
                data = sellmentdata
                break;
            case 'boxment':
                // 
                data = boxmentdata
                break;
            case 'userment':
                data = usermentdata
                break;
            case 'staffment':
                // 
                data = staffmentdata
                break;
            case 'datament':
                // 
                data = datamentdata
                break;
        }
        commit(types.ELMENU_SWITCH, { data })
    }
}

const mutations = {
    [types.HEADERNAV_SWITCH](state, { data }) {
        state.headernav = data
    },
    [types.ELMENU_SWITCH](state, { data }) {
        state.elmenu = data
    }
}

export default {
    state,
    getters,
    actions,
    mutations
}



