<template>
<div id="classeditor" class="right-content">
    <topbar :bone="bone" :btwo="btwo"></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">

                    <el-form-item label="" prop="parentId1" v-if="type === 's' || type === 'm'">
                        <el-select v-model="form.parentId1" placeholder="">
                            <el-option :label="item.number + ' ' + item.bigclass" :value="item.id" :key="item.id" v-for="item in nbigclass">
                                {{ item.number }} {{ item.bigclass }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="parentId2" v-if="type === 's'">
                        <el-select v-model="form.parentId2" placeholder="" no-data-text="">
                            <el-option :label="item.number + ' ' + item.bigclass" :value="item.id" :key="item.id" v-for="item in nmediumclass">
                                {{ item.number }} {{ item.bigclass }}
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item :label="label" prop="title">
                        <el-input v-model="form.title" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="number">
                        <el-input v-model="form.number" placeholder="0102" :disabled="true"></el-input>
                    </el-form-item>

                    <el-form-item>
                        <a href="http://bapi.binguobox.com/images/1491039976.jpg" class="f-color f-link" target="_blank">&gt;</a>

                        <!-- <el-button type="primary" @click="">&gt;</el-button> -->
                    </el-form-item>

                    <el-form-item label="">
                        <el-switch on-text="" off-text="" v-model="switchStatus"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">

            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submit('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// /
import api from '../../api/api.js'
import cancel from '../common/cancel.vue'
import topbar from '../common/topbar.vue'

export default {
    data() {
        // 
        var checkNumber = (rule, value, callback) => {
            if (!value) {
                return callback(new Error(''))
            }
            let val = Number(value)
            if (!Number.isInteger(val)) {
                callback(new Error(''))
            } else {
                callback()
            }
        }
        return {
            type: this.$route.params.type,
            id: this.$route.params.id,
            label: '',
            bone: '',
            btwo: '',
            form: {
                parentId1: '',
                parentId2: '',
                title: '',
                number: ''
            },
            switchStatus: true,
            rules: {
                parentId1: [{ required: true, message: '', trigger: 'change' }],
                parentId2: [{ required: true, message: '', trigger: 'blur' }],
                title: [{ required: true, message: '', trigger: 'blur' }],
                number: [
                    { required: true, message: ''},
                    { validator: checkNumber, trigger: 'blur' }
                ]
            },
        }
    },
    created() {
        // 
        // 
        // 
        switch (this.type) {
            case 'b':
                this.label = ''
                this.bone = ''
                this.btwo = ''
                break;
            case 'm':
                this.label = ''
                this.bone = ''
                this.btwo = ''
                this.$store.dispatch('getClass', { type: 1 })
                break;
            case 's':
                this.label = ''
                this.bone = ''
                this.btwo = ''
                this.$store.dispatch('getClass', { type: 1 })
                break;
            default:
        }
        this.getClassInfo({ id: this.id })

    },
    computed: {
        nbigclass() { return this.$store.getters.nbigclassdata },
        nmediumclass() { return this.$store.getters.nmediumclassdata }
    },
    methods: {
        submit(formName) {
            let that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    let param = this.form
                    param.id = this.id
                    param.status = this.switchStatus === true ? 1 : 0

                    api.apiCommunication('/Material/UpdateProductClass', param, function (response) {
                        if (response.status === '200') {
                            that.$message({
                                message: '',
                                type: 'success'
                            })
                            // 
                            that.$router.go(-1)
                        } else {
                            that.$alert('', '', { confirmButtonText: '', type: 'error' })
                        }
                    })

                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        },
        getClassInfo(param) {
            // 
            let that = this
            api.apiCommunication('/Material/getProductClassInfo', param, function (response) {
                if (response.status === '200') {
                    let _data = response.data
                    that.form.parentId1 = _data.parentId1
                    that.form.parentId2 = _data.parentId2
                    that.form.title = _data.title
                    that.form.number = _data.number
                    that.switchStatus = _data.status === '1' ? true : false
                } else {
                    that.$alert('', '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    },
    components: {
        topbar,
        cancel
    }
}
</script>



