<template>
<div id="supplierlist" class="right-content">
    <el-row class="mb-10">
        <el-col :span="24" class="el-item display-table top-toolbar-complex">
            <div class="toolbar-path tn"></div>
            <div class="toolbar-function tr">
                <el-form :model="toolbarFrom" :inline="true" class="tr">
                    <el-form-item>
                        <el-button type="primary" @click="routerPush('new')"></el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-input placeholder="" class="function-search" icon="search" v-model="toolbarFrom.searchkey" :on-icon-click="searchToolbar"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="searchToolbar"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-col>
    </el-row>

    <el-row class="mb-15">
        <el-col :span="24" class="el-item pa-10">
            <el-table :data="supplierList.list" :stripe="true" class="w-100">
                <el-table-column prop="id" label="" width="80"></el-table-column>
                <el-table-column prop="company" label=""></el-table-column>
                <el-table-column prop="business_number" label="" width="180"></el-table-column>
                <el-table-column prop="name" label="" width="100"></el-table-column>
                <el-table-column label="" width="70">
                    <template scope="scope">
                        <el-popover ref="popover" placement="right" width="312" trigger="click">
                            <ul class="payee-datawrap">
                                <li>
                                    <div>:</div>
                                    <div>{{ scope.row.name }}</div>
                                </li>
                                <li>
                                    <div>:</div>
                                    <div>{{ scope.row.phone }}</div>
                                </li>
                                <li>
                                    <div>:</div>
                                    <div>{{ scope.row.general_taxpayer | taxpayer }}</div>
                                </li>
                                <li>
                                    <div>:</div>
                                    <div>{{ scope.row.bank }}</div>
                                </li>
                                <li>
                                    <div>:</div>
                                    <div>{{ scope.row.bank_account }}</div>
                                </li>
                            </ul>
                        </el-popover>
                        <el-button v-popover:popover class="" size="mini"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="service" label="" width="300"></el-table-column>
                <el-table-column label="" width="140">
                    <template scope="scope">
                        <el-button type="primary" size="small" @click="routerPush(scope.row.id)"></el-button>
                        <el-button type="danger" size="small" @click="deleteItem(scope.row.id)"></el-button>
                    </template>
			  	</el-table-column>
			</el-table>

			<el-pagination @current-change="handleCurrentChange" :page-size="20" :current-page="1" layout="total, prev, pager, next, jumper" :total="supplierList.count">
            </el-pagination>
		</el-col>
	</el-row>

</div>
</template>

<script type="text/javascript">
// 
import api from '../../api/api.js'
import '../../static/style/datament/supplierList.scss'

export default {
    data() {
        return {
            toolbarFrom: {
                searchkey: ''
            },
            searchBtn: 0
        }
    },
    created() {
        this.$store.dispatch('getSupplierList', { page: 1 })
    },
    computed: {
        supplierList() { return this.$store.getters.supplierlist }
    },
    methods: {
        searchToolbar(val) {
            let param = {
                page: (typeof val === "number") ? val : 1
            }
            if(this.toolbarFrom.searchkey !== ''){
                param.key = this.toolbarFrom.searchkey
            }
            this.$store.dispatch('getSupplierList', param).then(() => {
                if(!(typeof val === "number")){
                    this.$message({
                        message: '',
                        type: 'success'
                    })
                }
            })
            this.searchBtn++
        },
        routerPush(id) {
            this.$router.push({ name: 'supplierEditor', params: { id: id } })
        },
        deleteItem(id) {
            var that = this
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let param = {
                    id: id
                }
                api.apiCommunication('/Material/DeleteSupplier', param, function (response) {
                    if (response.status === '200') {
                        that.$message({
                            message: '',
                            type: 'success'
                        })
                        // 
                        this.$store.dispatch('getSupplierList', { page: 1 })
                    } else {
                        that.$alert('', '', { confirmButtonText: '', type: 'error' })
                    }
                })
            }).catch(() => {})
        },
        handleCurrentChange(val) {
            if(this.searchBtn > 0 && this.toolbarFrom.searchkey !== '') {
                this.searchToolbar(val)
                return false
            }
            this.$store.dispatch('getSupplierList', { page: val })
        }
    },
    filters: {
        taxpayer(value) {
            return value === '1' ? '' : ''
        }
    }
}
</script>



