<template>
<div id="supplieredior" class="right-content">
    <topbar bone="" :btwo="btwo"></topbar>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item editor-form display-table">
            <div class="form-left">
                <el-form ref="form" :rules="rules" label-position="right" :model="form" label-width="150px">
                    <el-form-item label="" prop="company">
                        <el-input v-model="form.company" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="business_number">
                        <el-input v-model.number="form.business_number" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="general_taxpayer">
                        <el-select v-model="form.general_taxpayer" placeholder="">
                              <el-option label="" value="1"></el-option>
                              <el-option label="" value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="phone">
                        <el-input v-model.number="form.phone" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="bank">
                        <el-input v-model="form.bank" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="bank_account">
                        <el-input v-model.number="form.bank_account" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="service">
                        <el-input v-model="form.service" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
            </div>
            <div class="form-right">
                <image-upload :imageUrl="imageUrl" @increment="handleAvatarScucess"></image-upload>
                <p class="mt-10"> </p>
            </div>
        </el-col>
    </el-row>
    <el-row class="mb-10">
        <el-col :span="24" class="el-item pa-20">
            <p class="theme-p mb-10"></p>
            <el-button type="primary" @click="submit('form')"></el-button>
            <cancel></cancel>
        </el-col>
    </el-row>
</div>
</template>

<script>
// 
import api from '../../api/api.js'
import topbar from '../common/topbar.vue'
import cancel from '../common/cancel.vue'
import imageUpload from '../common/imageUpload.vue'
import '../../static/style/datament/supplierEditor.scss'

export default {
    data() {
        return {
            imageUrl: '',
            btwo: '',
            form: {
                company: '',
                business_number: '',
                general_taxpayer: '',
                name: '',
                phone: '',
                bank: '',
                bank_account: '',
                service: ''
            },
            rules: {
                company: [{ required: true,message: '',trigger: 'blur' }],
                business_number: [
                    { required: true, message: '' },
                    { type: 'number', message: ''}
                ],
                general_taxpayer: [{ required: true, message: '', trigger: 'change' }],
                name: [{ required: true, message: '', trigger: 'blur' }],
                phone: [
                    { required: true, message: '' },
                    { type: 'number', message: ''}
                ],
                bank: [{ required: true, message: '', trigger: 'blur' }],
                bank_account: [
                    { required: true, message: '' },
                    { type: 'number', message: ''}
                ]
            },
            fileList: [],
            supplierId: this.$route.params.id
        }
    },
    created() {
        if(this.supplierId !== 'new'){
            this.btwo = ''
            let that = this,
                param = {
                    id: this.supplierId
                }
            api.apiCommunication('/Material/SupplierInfo', param, function (response) {
                if (response.status === '200') {
                    let _data = response.data
                    that.form.company = _data.company
                    that.form.business_number = Number(_data.business_number)
                    that.form.general_taxpayer = _data.general_taxpayer
                    that.form.name = _data.name
                    that.form.phone = Number(_data.phone)
                    that.form.bank = _data.bank
                    that.form.bank_account = Number(_data.bank_account)
                    that.form.service = _data.service
                    that.imageUrl = _data.img
                } else {
                    that.$alert('', '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    },
    computed: {

    },
    methods: {
        submit(formName) {
            var that = this
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let param = this.form
                    param.id = this.supplierId
                    param.img = this.imageUrl

                    if(this.supplierId !== 'new') {
                        api.apiCommunication('/Material/UpdateSupplier', param, function (response) {
                            if (response.status === '200') {
                                that.$message({
                                    message: '',
                                    type: 'success'
                                })
                                // 
                                that.$router.go(-1)
                            } else {
                                that.$alert('', '', { confirmButtonText: '', type: 'error' })
                            }
                        })
                    } else {
                        api.apiCommunication('/Material/CreateSupplier', param, function (response) {
                            if (response.status === '200') {
                                that.$message({
                                    message: '',
                                    type: 'success'
                                })
                                // 
                                that.$router.go(-1)
                            } else {
                                that.$alert('', '', { confirmButtonText: '', type: 'error' })
                            }
                        })
                    }

                } else {
                    this.$alert('', '', { confirmButtonText: '' })
                }
            })
        },
        handleAvatarScucess(row) {
            // 
            this.imageUrl = row
        }
    },
    components: {
        topbar,
        imageUpload,
        cancel
    }
}
</script>



